<template>
  <section class="create__cntr">
    <div class="account__cntr">
      <!-- Field for ID -->
      <b-field label="ID">
        <b-input placeholder="ID" v-model="id"> </b-input>
      </b-field>
      <!-- Field for Name -->
      <b-field label="Name *">
        <b-input
          placeholder="Name"
          type="text"
          required
          validation-message="Please enter name"
        >
        </b-input>
      </b-field>
      <!-- Field for Contact Number -->
      <b-field label="Contact number *">
        <b-input
          placeholder="Number"
          type="number"
          required
          validation-message="Please enter Phone number"
        >
        </b-input>
      </b-field>
      <!-- Field for Email -->
      <b-field label="Email *">
        <b-input
          placeholder="email@gmail.com"
          type="text"
          required
          validation-message="Please enter valid email address"
          pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
        >
        </b-input>
      </b-field>
      <!-- Field for Address -->
      <b-field label="Address *">
        <b-input
          type="textarea"
          required
          minlength="10"
          maxlength="100"
          placeholder="Enter your address here"
        >
        </b-input>
      </b-field>
      <!-- Field for field -->
      <div class="field">
        <b-checkbox
          v-model="checkboxCustom"
          true-value="Restricted"
          false-value="Not Restricted"
        >
          {{ checkboxCustom }}
        </b-checkbox>
      </div>
      <!-- Field for IP address -->
      <b-field label="IP address *">
        <b-input
          placeholder="IP address"
          type="number"
          required
          validation-message="Please enter IP address"
        >
        </b-input>
      </b-field>
      <!-- Field for Plans Dropdown -->
      <b-field label="Plans *">
        <b-dropdown v-model="selectedPlan" aria-role="list" required>
          <button class="button is-info" type="button" slot="trigger">
            <template v-if="selectedPlan">
              <span>{{ selectedPlan }}</span>
            </template>
            <template v-else>
              <span>Select Plans</span>
            </template>
            <b-icon icon="menu-down"></b-icon>
          </button>

          <b-dropdown-item :value="'Basic'" aria-role="listitem">
            <div class="media">
              <div class="media-content">
                <h3>Basic</h3>
              </div>
            </div>
          </b-dropdown-item>

          <b-dropdown-item :value="'Economic'" aria-role="listitem">
            <div class="media">
              <div class="media-content">
                <h3>Economic</h3>
              </div>
            </div>
          </b-dropdown-item>

          <b-dropdown-item :value="'Premium'" aria-role="listitem">
            <div class="media">
              <div class="media-content">
                <h3>Premium</h3>
              </div>
            </div>
          </b-dropdown-item>
        </b-dropdown>
      </b-field>
      <!-- Field for Customization -->
      <b-field label="Customization *">
        <b-input
          placeholder="Customization"
          type="text"
          required
          validation-message="Please enter the Customization"
        >
        </b-input>
      </b-field>
      <!-- Field for Comments -->
      <b-field label="Comments *">
        <b-input
          type="textarea"
          required
          minlength="0"
          maxlength="300"
          placeholder="Enter your Comments here"
        >
        </b-input>
      </b-field>
      <span>
        <b-button type="is-info">Submit</b-button>
        <b-button type="is-info">Cancel</b-button>
      </span>
    </div>
  </section>
</template>
<script>
export default {
  name: "Create Account",
  data() {
    return {
      checkbox: false,
      checkboxCustom: "Not Restricted",
      selectedPlan: null,
    };
  },
};
</script>
<style lang="scss" scoped>
.create__cntr {
  min-width: 80%;
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
  margin: 120px 0 40px 300px;
  .account__cntr {
    display: table;
    margin: 0 auto;
    width: 50%;
    border: 5px solid #e5e5e5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 20px;
  }
}
</style>



