<template>
  <div id="app">
    <header-component />
    <sidebar-menu
      :menu="menu"
      :theme="selectedTheme"
      :show-one-child="true"
      class="sidebar__cntr"
    />
    <router-view class="router__cntr" />
  </div>
</template>

<script>
import { SidebarMenu } from "vue-sidebar-menu";
import "vue-sidebar-menu/dist/vue-sidebar-menu.css";
import HeaderComponent from "./components/headerComponent/index.vue";

export default {
  components: {
    SidebarMenu,
    HeaderComponent
  },
  name: "App",
  data() {
    return {
      selectedTheme: "white-theme",
      menu: [
        {
          href: "/",
          title: "Dashboard",
          icon: "fa fa-tasks"
        },
        {
          title: "Accounts",
          icon: "fa fa-user-circle",
          child: [
            {
              href: "/accounts/createAccount",
              title: "Create account"
            }
          ]
        },
        {
          title: "DCF",
          icon: "fa fa-chart-area",
          child: [
            {
              href: "/DCF/createDcf",
              title: "Create DCF"
            }
          ]
        },
        {
          href: "/settings",
          title: "Settings",
          icon: "fa fa-cog"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
@import "./assets/scss/_utils.scss";

* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
}
html {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
}
.sidebar__cntr {
  width: 20%;
  background-color: #e5e5e5 !important;
  display: inline-block;
  vertical-align: middle;
  position: fixed;
  margin-top: 90px;
  z-index: 0;
}

.router__cntr {
  display: inline-block;
  vertical-align: middle;
}
</style>



