<template>
  <div class="completed__Dcf__cntr">
    <div class="filter-wrapper">
      <span class="mdi mdi-filter filter-icon mdi-18px"></span>
      <div class="filter-options">
        <b-select
          placeholder="Select an option"
          @input="filterOptionHandler($event)"
        >
          <option value="id">ID</option>
          <option value="fullname">User Name</option>
          <option value="mobile">Contact</option>
          <option value="email">Email</option>
          <option value="dfcNo">DCF No</option>
          <option value="plan">Plan</option>
          <option value="created">Created</option>
        </b-select>
      </div>
      <div class="filter-input">
        <b-input
          placeholder="Add Filter"
          type="search"
          v-model="searchInput"
          icon-clickable
          @keyup.native.enter="searchHandler"
          @focus="openFilterOptions = true"
        >
        </b-input>
      </div>
    </div>
    <b-table
      :data="data"
      :columns="columns"
      :checked-rows.sync="checkedRows"
      checkable
      :checkbox-position="checkboxPosition"
      :paginated="isPaginated"
      :per-page="perPage"
      :current-page.sync="currentPage"
      :pagination-simple="isPaginationSimple"
      :pagination-position="paginationPosition"
      :striped="isStriped"
      default-sort="users"
      aria-next-label="Next page"
      aria-previous-label="Previous page"
      aria-page-label="Page"
      aria-current-label="Current page"
    >
      <template slot-scope="props">
        <b-table-column>
          <span> {{ props.row.id }} </span>
        </b-table-column>
        <b-table-column>
          <span class="profile--img__cntr">
            <!-- <img :src="props.row.profile_img_url" alt="" /> -->
            <img src="../../assets/images/user2.png" alt="" />
          </span>
          <span class="profile--info__cntr">
            <div class="name__txt">{{ props.row.fullname }}</div>
            <div class="email__txt">{{ props.row.email }}</div>
          </span>
        </b-table-column>
        <b-table-column>
          <span> {{ props.row.mobile }} </span>
        </b-table-column>
        <b-table-column>
          <span> {{ props.row.dfcNo }} </span>
        </b-table-column>
        <b-table-column>
          <span :class="props.row.plan"> {{ props.row.plan }} </span>
        </b-table-column>
        <b-table-column>
          <span> {{ props.row.created }} </span>
        </b-table-column>
        <b-table-column>
          <span @click="editRow">
            <b-icon
              icon="mdi mdi-pencil"
              size="is-small"
              class="edit__icon"
            ></b-icon>
          </span>
        </b-table-column>
        <b-table-column>
          <span @click="deleteRow">
            <b-icon
              icon="mdi mdi-delete"
              size="is-small"
              class="delete__icon"
            ></b-icon>
          </span>
        </b-table-column>
      </template>
      <template slot="bottom-left">
        <b>Total checked</b>: {{ checkedRows.length }}
      </template>
    </b-table>
  </div>
</template>
<script>
export default {
  name: "Completed DCF",
  data() {
    return {
      selectedFilterOpt: "",
      selectedFilterOpt: "",
      pageIndex: 1,
      checkboxPosition: "left",
      checkedRows: [],
      isPaginated: true,
      isPaginationSimple: false,
