        },
        {
          field: "contact",
          label: "Contact"
        },
        {
          field: "dcf_no",
          label: "DCF No"
        },
        {
          field: "plan",
          label: "Plan"
        },
        {
          field: "created",
          label: "Created"
        },
        {
          field: "edit",
          label: ""
        },
        {
          field: "delete",
          label: ""
        }
      ]
    };
  },
  methods: {
    editRow() {
      this.$buefy.dialog.confirm({
        message: "Continue on this task?",
        onConfirm: () => this.$buefy.toast.open("User confirmed")
      });
    },
    deleteRow() {
      this.$buefy.dialog.confirm({
        title: "Deleting Row",
        message:
          "Are you sure you want to <b>delete</b> this ID? This action cannot be undone.",
        confirmText: "Delete Account",
        type: "is-danger",
        hasIcon: true,
        onConfirm: () => this.$buefy.toast.open("Account deleted!")
      });
    },
    searchHandler(value) {
      let payload = {
        pageSize: 12,
        pageIndex: this.pageIndex
      };
      let queryParam = `pageIndex=1&pageSize=12&${this.selectedFilterOpt}=${this.searchInput}`;
      this.data(queryParam).then(res => {
        // if (res && res.data && res.data.communications) {
        //   this.setCommunicationData(res.data.communications);
        // }
      });
    },
    filterOptionHandler(event) {
      console.log(event);
      this.selectedFilterOpt = event;
    }
