</script>
<style lang="scss">
.completed__Dcf__cntr {
  min-width: 76%;
  max-width: 100%;
  text-align: center;
  margin-top: 120px;
  margin-left: 320px;
  .filter-wrapper {
    margin: 10px;
    margin-bottom: 40px;
    width: 60%;
    border: 1px solid #dddddd;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    .filter-icon {
      padding: 0 10px;
      border-right: 1px solid #dddddd;
    }
    .filter-options,
    .filter-input {
      display: inline-block;
      border: none;
    }
    .filter-options {
      select {
        border: none;
        &:focus {
          outline: 0;
          border: none;
          box-shadow: none;
        }
      }
    }
    .filter-input {
      width: 100%;
    }
    .filter-input .control .input {
      border: none;
      box-shadow: none;
    }
  }
  .profile--img__cntr {
    display: inline-block;
    vertical-align: middle;
  }
  .profile--info__cntr {
    display: inline-block;
    vertical-align: middle;
    .name__txt {
      font-weight: bold;
      font-size: 14px;
      line-height: 22px;
    }
  }
  .Premium {
    color: #00afef;
  }
  .Basic {
    color: #9252fc;
  }
  .Economic {
    color: #fa813d;
  }
  .email__txt {
    color: #7a7777;
  }
  .edit__icon,
  .delete__icon {
    cursor: pointer;
  }
}
</style>



