<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  mounted() {
    this.renderChart(
      {
        labels: [
          "Mercury",
          "Venus",
          "Earth",
          "Mars",
          "Jupiter",
          "Saturn",
          "Uranus",
          "Neptune"
        ],
        datasets: [
          {
            // first line graph
            label: "Number of Moons",
            data: [0, 0, 1, 2, 67, 62, 27, 14],
            backgroundColor: [
              "rgba(19, 192, 80, 0.3)" // Green
            ],
            borderColor: ["#13C050"],
            borderWidth: 3
          },
          {
            // second line graph
            label: "Planet Mass",
            data: [4.8, 12.1, 12.7, 6.7, 139.8, 116.4, 50.7, 49.2],
            backgroundColor: [
              "rgba(250, 129, 61, 0.3)" //orange
            ],
            borderColor: ["#FA813D"],
            borderWidth: 3
          },
          {
            // third line graph
            label: "Third graph",
            data: [10, 20, 130, 140, 30, 50, 10, 0],
            backgroundColor: [
              "rgba(146, 82, 252, 0.3)" // Purple
            ],
            borderColor: ["#9252FC"],
            borderWidth: 3
          }
        ]
      },
      { responsive: true, maintainAspectRatio: false }
    );
  }
};
</script>



