import Vue from "vue";
import Router from "vue-router";
import Dashboard from "@/components/Dashboard/dashboard.vue";
import TotalAccounts from "@/components/TotalAccounts/index.vue";
import TotalDcf from "@/components/TotalDcf/index.vue";
import OngoingDcf from "@/components/OngoingDcf/index.vue";
import CompletedDcf from "@/components/CompletedDcf/index.vue";
import createAccount from "@/components/CreateAccount/index.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Admin Panel",
      component: Dashboard
    },
    {
      path: "/toTotalAccounts",
      name: "Total Accounts",
      component: TotalAccounts
    },
    {
      path: "/toTotalDcf",
      name: "Total DCF",
      component: TotalDcf
    },
    {
      path: "/toOngoingDcf",
      name: "Ongoing DCF",
      component: OngoingDcf
    },
    {
      path: "/toCompletedDcf",
      name: "Completed DCF",
      component: CompletedDcf
    },
    {
      path: "/accounts/createAccount",
      name: "Create Account",
      component: createAccount
    }
  ]
});



