<template>
  <div class="dashboard__cntr">
    <div class="dashboard__cntr__grid__cntr">
      <div class="grid__cntr--total__acounts" @click="accountsClicked">
        <img src="../../assets/images/totalAccounts.png" alt="" />
        <p class="grid__count--total__acounts">21.2k</p>
        <p class="grid__txt--total__acounts">Total accounts</p>
        <p class="grid__status--cntr--total__acounts">
          <span class="grid__level--increse--total__acounts"></span>
          <span class="grid__level--percent--increse--total__acounts"
            >24.2%</span
          >
        </p>
      </div>
      <div class="grid__cntr--total__dcf" @click="totalDcfClicked">
        <img src="../../assets/images/totalDcf.png" alt="" />
        <p class="grid--count--total__dcf">1.6k</p>
        <p class="grid__txt--total__dcf">Total DCF</p>
        <p class="grid__status--cntr--total__dcf">
          <span class="grid__level--decrese--total__dcf"></span>
          <span class="grid__level--percent--decrese--total__dcf">10.71%</span>
        </p>
      </div>
      <div class="grid__cntr--ongoing__dcf" @click="ongoingDcfClicked">
        <img src="../../assets/images/ongoingDcf.png" alt="" />
        <p class="grid--count--ongoing__dcf">7.2k</p>
        <p class="grid__txt--ongoing__dcf">Ongoing DCF</p>
        <p class="grid__status--cntr--ongoing__dcf">
          <span class="grid__level--increse--ongoing__dcf"></span>
          <span class="grid__level--percent--increse--ongoing__dcf"
            >91.00%</span
          >
        </p>
      </div>
      <div class="grid__cntr--completed__dcf" @click="completedDcfClicked">
        <img src="../../assets/images/completedDcf.png" alt="" />
        <p class="grid--count--completed__dcf">1.3k</p>
        <p class="grid__txt--completed__dcf">Completed DCF</p>
        <p class="grid__status--cntr--completed__dcf">
          <span class="grid__level--decrese--completed__dcf"></span>
          <span class="grid__level--percent--decrese--completed__dcf"
            >12.00%</span
          >
        </p>
      </div>
    </div>
    <div class="dashboard__cntr__graph__cntr">
      <div class="graph__cntr--first">
        <line-chart />
      </div>
      <div class="graph__cntr--second">
        <filled-line-chart />
      </div>
    </div>
    <div class="download__button">
      <b-button type="is-info">
        <span><b-icon icon="mdi mdi-download" size="is-small"> </b-icon></span>
        Download
      </b-button>
    </div>
    <div class="dashboard__cntr__table__cntr">
      <b-table
        :data="data"
        :columns="columns"
        :checked-rows.sync="checkedRows"
        checkable
        :checkbox-position="checkboxPosition"
        :paginated="isPaginated"
        :per-page="perPage"
        :current-page.sync="currentPage"
        :pagination-simple="isPaginationSimple"
        :pagination-position="paginationPosition"
        :striped="isStriped"
        aria-next-label="Next page"
        aria-previous-label="Previous page"
        aria-page-label="Page"
        aria-current-label="Current page"
      >
        <template slot-scope="props">
          <b-table-column>
            <span> {{ props.row.id }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.first_name }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.last_name }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.mobile }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.email }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.topic }} </span>
          </b-table-column>
          <b-table-column>
            <span> {{ props.row.dcfDetail }} </span>
          </b-table-column>
          <b-dropdown aria-role="list">
            <button
              class="button is-info"
              slot="trigger"
              slot-scope="{ active }"
            >
              <b-icon icon="mdi mdi-settings" size="is-small"></b-icon>
              <span>Action</span>
              <b-icon :icon="active ? 'menu-up' : 'menu-down'"></b-icon>
            </button>

            <b-dropdown-item aria-role="listitem">View</b-dropdown-item>
            <b-dropdown-item aria-role="listitem"
              >Active/Deactive</b-dropdown-item
            >
            <b-dropdown-item aria-role="listitem">Edit</b-dropdown-item>
            <b-dropdown-item aria-role="listitem">Trash</b-dropdown-item>
          </b-dropdown>
        </template>
        <template slot="bottom-left">
          <b>Total checked</b>: {{ checkedRows.length }}
        </template>
      </b-table>
    </div>
  </div>
</template>

<script>
import lineChart from "./lineChart.vue";
import filledLineChart from "./filledLineChart.vue";

export default {
  name: "Dashboard",
  components: {
    lineChart,
    filledLineChart
  },
  data() {
    return {
      checkboxPosition: "left",
      checkedRows: [],
      isPaginated: true,
      isPaginationSimple: false,
      paginationPosition: "bottom",
      isStriped: true,
      currentPage: 1,
      perPage: 5,
      data: [
        {
          id: 1,
          first_name: "Jesse",
          last_name: "Simmons",
          mobile: "+91-1234567890",
          email: "abcd@gmail.com",
          topic: "jsndsjnj",
          dcfDetail: "Completed",
          button: ""
        },
        {
          id: 2,
          first_name: "John",
          last_name: "Jacobs",
          mobile: "+91-0987654321",
          email: "edfg@gmail.com",
          topic: "mnkfjjn",
          dcfDetail: "In-Progress",
          button: ""
        },
        {
          id: 3,
          first_name: "Tina",
          last_name: "Gilbert",
          mobile: "+91-6789054321",
          email: "qwer@gmail.com",
          topic: "tweuwiewe",
          dcfDetail: "Scheduled",
          button: ""
        },
        {
          id: 4,
          first_name: "Clarence",
          last_name: "Flores",
          mobile: "+91-3214509876",
          email: "lopuy@gmail.com",
          topic: "dfkjdskfj",
          dcfDetail: "In-Progress",
          button: ""
        },
        {
          id: 5,
          first_name: "Anne",
          last_name: "Lee",
          mobile: "+91-7890654123",
          email: "gdhyd456@gmail.com",
          topic: "jsndsjnj",
          dcfDetail: "Completed",
          button: ""
        }
      ],
      columns: [
        {
          field: "sl Num",
          label: "SL No",
          width: "70",
          numeric: true
        },
        {
          field: "first_name",
          label: "First Name"
        },
        {
          field: "last_name",
          label: "Last Name"
        },
        {
          field: "mobile",
          label: "Mobile"
        },
        {
          field: "email",
          label: "Email"
        },
        {
          field: "topic",
          label: "Topic"
        },
        {
          field: "dcfDetails",
          label: "DCF details"
        },
        {
          field: "button",
          label: ""
        }
      ]
    };
  },
  methods: {
    accountsClicked() {
      this.$router.push({
        path: "/toTotalAccounts"
      });
    },
    totalDcfClicked() {
      this.$router.push({
        path: "/toTotalDcf"
      });
    },
    ongoingDcfClicked() {
      this.$router.push({
        path: "/toOngoingDcf"
      });
    },
    completedDcfClicked() {
      this.$router.push({
        path: "/toCompletedDcf"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.dashboard__cntr {
  min-width: 80%;
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  margin-top: 120px;
  margin-left: 300px;
  &__grid__cntr {
    .grid__cntr--total__acounts,
    .grid__cntr--total__dcf,
    .grid__cntr--ongoing__dcf,
    .grid__cntr--completed__dcf {
      display: inline-block;
      vertical-align: middle;
      width: 20%;
      cursor: pointer;
      padding: 10px;
      margin-right: 50px;
      border: 5px solid #e5e5e5;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      .grid__count--total__acounts,
      .grid--count--total__dcf,
      .grid--count--ongoing__dcf,
      .grid--count--completed__dcf {
        font-weight: bold;
        font-size: 36px;
        line-height: 44px;
      }
      .grid__txt--total__acounts,
      .grid__txt--total__dcf,
      .grid__txt--ongoing__dcf,
      .grid__txt--completed__dcf {
        font-size: 12px;
        line-height: 19px;
        color: #7a7777;
      }
      .grid__status--cntr--total__acounts,
      .grid__status--cntr--total__dcf,
      .grid__status--cntr--ongoing__dcf,
      .grid__status--cntr--completed__dcf {
        .grid__level--increse--total__acounts,
        .grid__level--increse--ongoing__dcf {
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 10px solid #13c050;
          display: inline-block;
          vertical-align: middle;
          margin-right: 4px;
        }
        .grid__level--decrese--completed__dcf,
        .grid__level--decrese--total__dcf {
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-top: 10px solid #f80e0e;
          display: inline-block;
          vertical-align: middle;
          margin-right: 4px;
        }
        .grid__level--percent--increse--total__acounts,
        .grid__level--percent--increse--ongoing__dcf {
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          color: #13c050;
          display: inline-block;
          vertical-align: middle;
        }
        .grid__level--percent--decrese--total__dcf,
        .grid__level--percent--decrese--completed__dcf {
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          color: #f80e0e;
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }
  &__graph__cntr {
    .graph__cntr--first,
    .graph__cntr--second {
      display: inline-block;
      vertical-align: middle;
      border: 5px solid #e5e5e5;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 30px;
      margin-right: 50px;
      width: 44.5%;
      padding: 20px;
    }
  }
  .download__button {
    margin-top: 30px;
    position: absolute;
    right: 24px;
  }
  &__table__cntr {
    margin-top: 40px;
    width: 98%;
    .table thead {
      background-color: #e5e5e5 !important;
    }
  }
}
</style>



