<template>
  <header>
    <span class="header__logo" @click="homePage">
      <img
        src="../../assets/images/dataKaptLogo.png"
        class="header__logo--img"
        alt="logo is unavailable"
      />
    </span>
    <span class="header__title">{{ this.$route.name }}</span>
    <template>
      <section class="search__bar">
        <!-- <span>{{ selected }}</span> -->
        <b-field>
          <b-autocomplete
            rounded
            v-model="name"
            :data="filteredDataArray"
            placeholder="Search"
            icon="magnify"
            clearable
            @select="option => (selected = option)"
          >
            <template slot="empty">No results found</template>
          </b-autocomplete>
        </b-field>
      </section>
    </template>
    <div class="profile__dataCntr">
      <div class="profile__dataCntr--image">
        <img
          src="../../assets/images/Harish_C_Jingade.jpg"
          class="profile__Image"
          alt="Profile image is not there"
        />
      </div>
      <div class="profile__dataCntr--notification">
        <span class="notification__txt">3</span>
      </div>
    </div>
  </header>
</template>
<script>
export default {
  name: "headerIndex",
  data() {
    return {
      data: [
        "Angular",
        "Angular 2",
        "Aurelia",
        "Backbone",
        "Ember",
        "jQuery",
        "Meteor",
        "Node.js",
        "Polymer",
        "React",
        "RxJS",
        "Vue.js"
      ],
      name: "",
      selected: null
    };
  },
  computed: {
    filteredDataArray() {
      return this.data.filter(option => {
        return (
          option
            .toString()
            .toLowerCase()
            .indexOf(this.name.toLowerCase()) >= 0
        );
      });
    }
  },
  methods: {
    homePage() {
      this.$router.push({
        path: "/"
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../assets/scss/_utils.scss";

header {
  width: 100%;
  position: fixed;
  background: #fff;
  z-index: 1;
  .header__logo {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    width: 20%;
    &--img {
      width: 250px;
      padding: 6px;
    }
  }
  .header__title {
    display: inline-block;
    vertical-align: middle;
    font-family: Futura;
    width: 14%;
    color: #000;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin: 4px;
    text-align: center;
  }
  .search__bar {
    display: inline-block;
    vertical-align: middle;
    width: 40%;
    .input {
      background: #f9f9f9;
    }
    .control.has-icons-left .input {
      border: none;
    }
    .control.has-icons-left .icon {
      color: #2292f9;
    }
    .input:focus {
      outline: none;
      box-shadow: none;
    }
  }
  .profile__dataCntr {
    width: 22%;
    display: inline-block;
    vertical-align: middle;
    text-align: right;

    &--image {
      width: 15%;
      display: inline-block;
      vertical-align: middle;
      margin-right: 40px;
      .profile__Image {
        border-radius: 50%;
        height: 40px;
        width: 40px;
      }
    }
    &--notification {
      width: 10%;
      height: 28px;
      display: inline-block;
      vertical-align: middle;
      text-align: center;
      background-color: #2292f9;
      .notification__txt {
        color: #fff;
      }
    }
  }
}
</style>



